<!--  -->
<template>
  <div class="i-student-all">
    <el-table
      :data="tableData"
      stripe
      style="width: 100%">
      <el-table-column
        label=""
        width="50"
        :filters="[
          { text: '', value: '1' },
          { text: '', value: '2' },
          { text: '', value: '3' },
          { text: '', value: '4' }
        ]"
        :filter-method="filterTag"
        :filter-multiple="false"
        filter-placement="top-start">
        <template scope="scope">
          <div class="i-student-table-ico"></div>
        </template>
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
      ></el-table-column>
      <el-table-column
        prop="level"
        label=""
        :formatter="formatter"
        sortable
        width="115"
      ></el-table-column>
      <el-table-column
        prop="type"
        label=""
        sortable
        width="90"
      ></el-table-column>
      <el-table-column
        prop="classroom"
        label=""
      ></el-table-column>
      <el-table-column
        prop="number"
        label=""
      ></el-table-column>
      <el-table-column
        prop="tel"
        label=""
        width="130"
      ></el-table-column>
      <el-table-column
        prop="address"
        label=""
        :formatter="formatter"
      ></el-table-column>
      <el-table-column
        prop="date"
        label=""
        sortable
        width="115"
      ></el-table-column>
      <el-table-column
        label="">
        <template scope="scope">
          <el-popover
            placement="bottom"
            popper-class="i-popover"
            width="80"
            :offset="-30"
            trigger="click">
            <div class="i-t-d-popover">
              <router-link to="/student/detail"></router-link>
              <a href="javascript:void(0)"></a>
              <a href="javascript:void(0)"></a>
              <a href="javascript:void(0)"></a>
              <a href="javascript:void(0)"></a>
            </div>
            <div class="el-icon-more i-student-popover" slot="reference"></div>
          </el-popover>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
  import all from '../../mock/student/all'

  export default {
    methods: {
      formatter (row, column) {
        if (column.property === 'type') {
          return '' + row.type
        } else if (column.property === 'level') {
          return '' + row.level
        } else {
          return '' + row.address
        }
      },
      filterTag (value, row) {
        return row.tag === value
      },
      getData () {
        this.tableData = all.getData()
      }
    },
    data () {
      return {
        tableData: []
      }
    },
    mounted () {
      this.$nextTick(() => {
        this.getData()
      })
    }
  }
</script>



