import Mock from 'mockjs/dist/mock-min'

const options = [
  {
    value: 1,
    label: '1'
  },
  {
    value: 2,
    label: '2'
  },
  {
    value: 3,
    label: '3'
  }
]

const list = {
  name: '',
  classroom: '',
  type: '',
  level: '',
  teacher: '',
  students: 'xx',
  date: '2017-05-04',
  addr: 'xxxxx',
  resource: '12',
  progress: 'xx',
  summary: 'xx'
}

function createData () {
  let tmp = Mock.mock({
    'list|6': [{
      'id|+1': 1,
      'course|7': [{
        'active': function () {
          return Math.random() > 0.5
        },
        'text': ''
      }]
    }]
  })
  return tmp.list
}

export default {
  createData: createData,
  list: list,
  options: options
}



