import Mock from 'mockjs/dist/mock-min'

function createEventData () {
  let data = Mock.mock({
    'list': [{
      name: '',
      id: 1,
      number: 9,
      'list|1-20': [{
        title: '',
        content: 'B',
        sub: '2017-2-914:00-15:00',
        time: '2017-2-9'
      }]
    }, {
      name: '',
      id: 2,
      number: 6,
      'list|1-20': [{
        title: '',
        content: 'B',
        sub: '2017-2-914:00-15:00',
        time: '2017-2-9'
      }]
    }, {
      name: '',
      id: 3,
      number: 29,
      'list|1-20': [{
        title: '',
        content: 'B',
        sub: '2017-2-914:00-15:00',
        time: '2017-2-9'
      }]
    }]
  })
  return data.list
}

export default {
  createEventData: createEventData
}



