<template>
  <div class="i-system">
    <el-form class="i-form" :label-position="labelPosition" label-width="80px" style="width: 550px;" :model="information">
      <el-form-item label="">
        <div class="i-switch">
          <div class="i-switch-label"></div>
          <div class="i-switch-switch">
            <el-switch :width="58" v-model="value1" on-text="" off-text="" on-color="#4993FA" off-color="#CDCDDE"></el-switch>
          </div>
        </div>
        <div class="i-switch">
          <div class="i-switch-label"></div>
          <div class="i-switch-switch">
            <el-switch :width="58" v-model="value2" on-text="" off-text="" on-color="#4993FA" off-color="#CDCDDE"></el-switch>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="">
        <div class="i-info-timepicker" v-for="(time,index) in times" :key="index">
          <div class="i-info-timepicker-label">{{ time }}</div>
          <div class="i-info-timepicker-picker">
            <el-time-select
              style="width: 200px;"
              placeholder=""
              :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '18:30'
            }">
            </el-time-select>
          </div>
          <div class="i-info-timepicker-border"></div>
          <div class="i-info-timepicker-picker">
            <el-time-select
              placeholder=""
              style="width: 200px;"
              :picker-options="{
              start: '08:30',
              step: '00:15',
              end: '18:30'
            }">
            </el-time-select>
          </div>
        </div>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="information.name" placeholder="" style="width: 300px;">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="information.name" placeholder="" style="width: 550px;">
          <el-option
            v-for="item in levels"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-row :gutter="50">
          <el-col :span="12">
            <el-button type="primary" class="i-button i-confirm" style="width: 250px;"></el-button>
          </el-col>
          <el-col :span="12">
            <el-button type="primary" class="i-button i-cancel" style="width: 250px;"></el-button>
          </el-col>
        </el-row>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        value1: false,
        value2: true,
        labelPosition: 'top',
        information: {
          name: ''
        },
        times: ['', '', '', '', '', '', ''],
        options: [{
          value: 1,
          label: '1'
        }, {
          value: 2,
          label: '2'
        }, {
          value: 3,
          label: '3'
        }],
        levels: [{
          value: 1,
          label: ''
        }, {
          value: 2,
          label: ''
        }, {
          value: 3,
          label: ''
        }]
      }
    }
  }
</script>
<style lang="less">
  .i-system {
    width: 100%;
    .el-input__inner {
      border-radius: 2px;
    }
  }
</style>



