import Mock from 'mockjs/dist/mock-min'

const list = ['', '', '', '', '', '', '', '', '']

let data = Mock.mock({
  'list|1-50': [{
    'id|+1': 1,
    'name': function () {
      return '' + Math.ceil(Math.random() * 100)
    },
    'type': function () {
      return '' + Math.ceil(Math.random() * 9)
    },
    'level': function () {
      return '' + Math.ceil(Math.random() * 9)
    },
    'classroom': function () {
      return list[Math.floor(Math.random() * 3)] + '' + list[Math.floor(Math.random() * 9)] + ''
    },
    'teacher': function () {
      return '' + Math.ceil(Math.random() * 100)
    },
    'status': function () {
      return '' + Math.ceil(Math.random() * 9)
    },
    'score': function () {
      return 60 + Math.floor(Math.random() * 40)
    },
    'starttime': '2017-05-05',
    'endtime': '2017-05-05'
  }]
})

export default {
  tableData: data.list
}



