import Mock from 'mockjs/dist/mock-min'

function getData () {
  let data = Mock.mock({
    'list|1-30': [{
      'id|+1': 1,
      'type': '',
      'summary': '',
      'point': '',
      'level': '3',
      'date': '2017-06-08'
    }]
  })
  return data.list
}

export default { getData }



