import Mock from 'mockjs/dist/mock-min'

/**
 * 
 * @type {{name: string, level: string, type: string, summary: string, date: string}}
 */
let material = {
  name: '',
  level: '',
  type: '',
  summary: '(2)(12)(//)',
  date: '2017-06-12'
}

/**
 * 
 */
const getCourseWareList = () => {
  let data = Mock.mock({
    'list|1-15': [{
      'id|+1': 1,
      'name': function () {
        return '' + Math.ceil(Math.random() * 30)
      },
      'date': '2017-06-12',
      'level': function () {
        return '' + Math.ceil(Math.random() * 3) + ''
      },
      'total': function () {
        return '' + Math.ceil(Math.random() * 9) + ''
      }
    }]
  })
  return data.list
}

/**
 * 
 */
const getCoursePaperList = () => {
  let data = Mock.mock({
    'list|1-15': [{
      'id|+1': 1,
      'name': function () {
        return '' + Math.ceil(Math.random() * 30)
      },
      'level': function () {
        return '' + Math.ceil(Math.random() * 3) + ''
      },
      'time': function () {
        return Math.ceil(Math.random() * 3) + ''
      },
      'date': '2017-0-12',
      'total': function () {
        return Math.ceil(Math.random() * 30)
      }
    }]
  })
  return data.list
}

export default { material, getCourseWareList, getCoursePaperList }



