import Mock from 'mockjs/dist/mock-min'

const list = ['', '', '', '', '', '', '', '', '']

function getData () {
  let data = Mock.mock({
    'list|1-50': [{
      'id|+1': 1,
      'name': function () {
        return list[Math.floor(Math.random() * 3)] + '' + list[Math.floor(Math.random() * 9)] + ''
      },
      'level': function () {
        return Math.ceil(Math.random() * 9)
      },
      'type': function () {
        return Math.ceil(Math.random() * 9)
      },
      'teacher': function () {
        return '' + Math.ceil(Math.random() * 100)
      },
      'number': function () {
        return Math.ceil(Math.random() * 500)
      },
      'address': function () {
        return Math.ceil(Math.random() * 9)
      },
      'date': '2017-05-09'
    }]
  })
  return data.list
}

export default {
  getData: getData
}



